<template>
  <div class="map" ref="mapDiv" v-if="!loading">
    <SvgPanZoom
      :key="ratingField + selectedFed"
      class="map-container"
      :zoomEnabled="false"
      :dblClickZoomEnabled="false"
      :preventMouseEventsDefault="false"
      :contain="true"
      :zoomScaleSensitivity="0.1"
      @svgpanzoom="registerSvgPanZoom"
      @onZoom="clearOver"
    >
      <svg
        width="100%"
        xmlns="http://www.w3.org/2000/svg"
        viewBox="-20 0 1234.449 760.6203"
      >
        <path
          v-for="(reg, i) in coloredRegions"
          :key="reg.title + i"
          :title="reg.title"
          :d="reg.d"
          :transform="reg.transform"
          :style="{ fill: reg.fill, stroke: reg.stroke }"
          :class="{ hover: reg.title === overReg, disable: reg.disable }"
          :id="`path-${i}`"
          ref="path"
          @mouseover="onOver(reg, i, $event)"
          @mouseout="onOver(null, i)"
          @mouseleave="onOver(null, i)"
          @click="goToRegion(reg)"
        ></path>
        <g>
          <g v-for="(t, i) in showCenters" :key="'text-' + i">
            <circle
              stroke="#535dea"
              :cx="t.x"
              :cy="t.y - 4"
              r="13"
              class="point-circle"
              :class="{ hover: t.elem.title === overReg }"
              @mouseover="onOver(t.elem, i, $event)"
              @mouseout="onOver(null, i)"
              @mouseleave="onOver(null, i)"
              @click="goToRegion(t.elem)"
            ></circle>
            <text
              :x="t.x"
              :y="t.y"
              class="point-text"
              :class="{ hover: t.elem.title === overReg }"
            >
              {{ t.elem[ratingField] }}
            </text>
          </g>
        </g>
      </svg>
    </SvgPanZoom>
    <div
      class="map-rating"
      :style="{
        background: `linear-gradient(90deg, ${colorArr[0]} 0%, ${
          colorArr[colorArr.length - 1]
        } 100%)`,
      }"
    >
      <div class="rating-container">
        <div class="rating-title">{{ ratingField }}</div>
        <div class="rating-start">1</div>
        <div class="rating-end">
          {{ ratingFieldTitle ? regionDataArr.length : maxRating }}
        </div>
      </div>
    </div>
    <div
      class="map-info"
      :key="selectedRegion.title"
      @click="clearOver"
      v-if="showTooltip && selectedRegion"
    >
      <h3 class="rating-selected-title">
        <div
          class="rating-number"
          :style="{
            backgroundColor: selectedRegion[ratingField]
              ? undefined
              : '#d0d0d0',
            color: selectedRegion[ratingField] ? undefined : '#fefefe',
          }"
        >
          {{ selectedRegion[ratingField] || "-" }}
        </div>
        {{ selectedRegion.title }}
      </h3>
      <div class="tooltip-content">
        <div class="tooltip-items" v-if="ratingType === 0 || ratingType === 1">
          <div class="tooltip-item">
            Задач поставлено
            <span class="tooltip-item-val">{{
              selectedRegion["Задач поставлено"] || "-"
            }}</span>
          </div>
          <div class="tooltip-item">
            Задач отобрано
            <span class="tooltip-item-val">{{
              selectedRegion["Задач отобрано"] || "-"
            }}</span>
          </div>
          <div class="tooltip-item">
            Задач решено
            <span class="tooltip-item-val">{{
              selectedRegion["Задач решено"] || "-"
            }}</span>
          </div>
        </div>
        <div class="tooltip-items" v-if="ratingType === 0 || ratingType === 2">
          <div class="tooltip-item">
            Решений внедрено
            <span class="tooltip-item-val">{{
              selectedRegion["Задач внедрено"] || "-"
            }}</span>
          </div>
          <div class="tooltip-item">
            Цифровой наставник
            <span
              class="tooltip-item-val"
              v-if="selectedRegion['Цифровой наставник']"
              >по
              {{ selectedRegion["Цифровой наставник"] }}
              {{
                declOfNum(selectedRegion["Цифровой наставник"], [
                  "задаче",
                  "задачам",
                  "задачам",
                ])
              }}
            </span>
            <span class="tooltip-item-val" v-else>-</span>
          </div>
          <div class="tooltip-item">
            Разместил цифровую практику на СМАРТЕКЕ
            <span class="tooltip-item-val">{{
              selectedRegion["Разместил цифровую практику на СМАРТЕКЕ"] || "-"
            }}</span>
          </div>
        </div>
        <div class="tooltip-items" v-if="ratingType === 0 || ratingType === 3">
          <div class="tooltip-item">
            Data-хакатоны
            <span
              v-if="selectedRegion['Data-хакатоны']"
              class="tooltip-item-val"
              >{{ selectedRegion["Data-хакатоны"] }}
              {{
                declOfNum(selectedRegion["Data-хакатоны"], [
                  "участник",
                  "участника",
                  "участников",
                ])
              }}
            </span>
            <span class="tooltip-item-val" v-else>-</span>
          </div>
          <div class="tooltip-item">
            Data-debatЫ
            <span v-if="selectedRegion['Data-debatЫ']" class="tooltip-item-val">
              {{ selectedRegion["Data-debatЫ"] }}
              {{
                declOfNum(selectedRegion["Data-debatЫ"], [
                  "участник",
                  "участника",
                  "участников",
                ])
              }}
            </span>
            <span class="tooltip-item-val" v-else>-</span>
          </div>
          <div class="tooltip-item">
            Количество команд победителей из региона
            <span
              v-if="selectedRegion['Количество команд победителей из региона']"
              class="tooltip-item-val"
              >{{ selectedRegion["Количество команд победителей из региона"] }}
              {{
                declOfNum(
                  selectedRegion["Количество команд победителей из региона"],
                  ["команда", "команды", "команд"]
                )
              }}
            </span>
            <span class="tooltip-item-val" v-else>-</span>
          </div>
        </div>
      </div>
    </div>
    <el-select
      class="rating-select"
      v-model="selectedRating"
      placeholder="Категории рейтинга"
    >
      <el-option
        v-for="item in ratingSorts"
        :key="item.field"
        :label="item.title || item.field"
        :value="item.field"
      >
        <strong v-if="item.title === 'Общий рейтинг'">{{ item.title }}</strong>
        <span v-else>{{ item.title }}</span>
      </el-option>
    </el-select>
    <el-select
      class="fed-select"
      v-model="selectedFed"
      placeholder="Федеральный округ"
    >
      <el-option :label="'Все федеральные округа'" :value="null"></el-option>
      <el-option v-for="item in fedArr" :key="item" :label="item" :value="item">
      </el-option>
    </el-select>
    <div class="top-10" v-if="top10Arr.length">
      <h3>ТОП-10</h3>
      <div
        class="top-10-item"
        :key="item['Субъект РФ']"
        @mouseover="onOver(item, i, $event)"
        @mouseout="onOver(null, i)"
        @mouseleave="onOver(null, i)"
        @click="goToRegion(item)"
        :class="{ active: item.title === overReg }"
        v-for="(item, i) in top10Arr"
      >
        <div class="top-10-icon">
          {{ i + 1 }}
        </div>
        {{ item["Субъект РФ"] }}
      </div>
    </div>
    <div class="map-controls">
      <button class="map-zoom-in" type="button" title="zoom in" @click="zoomIn">
        +
      </button>
      <button
        class="map-zoom-out"
        type="button"
        title="zoom out"
        @click="zoomOut"
      >
        -
      </button>
    </div>
  </div>
</template>

<script>
import Gradient from "javascript-color-gradient";
import SvgPanZoom from "vue-svg-pan-zoom";
import axios from "axios";
const colorGradient = new Gradient();
export default {
  components: { SvgPanZoom },
  async created() {},
  data() {
    return {
      loading: true,
      mapDiv$: null,
      centers: [],
      colorGradient,
      svgpanzoom: null,
      regionsObj: {},
      regions: [],
      overReg: null,
      selectedFed: null,
      tooltipTimer: null,
      selectedRating: "Место в рейтинге",
      ratingSort: "asc",
      ratingField: "Место в рейтинге",
      ratingFieldTitle: "Общий рейтинг",
      showTooltip: false,
      ratingSorts: [
        {
          title: "Общий рейтинг",
          field: "Место в рейтинге",
          sort: "asc",
        },
        {
          title: "Рейтинг регионов по задачам",
          field: "Место по задачам",
          sort: "asc",
        },
        {
          title: "Ретинг по развитию сообщества",
          field: "Место по сообществам",
          sort: "asc",
        },
        {
          title: "Рейтинг по внедрению",
          field: "Место по внедрениям",
          sort: "asc",
        },
      ],
    };
  },
  async mounted() {
    this.listenPostMessage(async (e) => {
      try {
        const ev = JSON.parse(e.data);
        if (ev.event === "loadData") {
          console.log("loadData", ev.data);
          const { mapData, regionsData } = await this.loadData(ev.data);
          regionsData.forEach((e) => {
            const finded = mapData.find((r) => r.title === e["Субъект РФ"]);
            if (!finded) {
              console.log(e["Субъект РФ"]);
            } else {
              Object.assign(finded, e);
            }
          });
          const regionsObj = regionsData.reduce((sum, curr) => {
            sum[curr["Субъект РФ"]] = curr;
            curr.title = curr["Субъект РФ"];
            return sum;
          }, {});
          const regions = mapData.filter((e) => !!regionsObj[e.title]);
          mapData.forEach((e) => {
            Object.assign(e, regionsObj[e.title]);
          });
          this.regions = regions;
          this.regionsObj = regionsObj;
          await this.$nextTick();
          this.loading = false;
        }
      } catch (error) {}
    });
    window.top.postMessage(
      JSON.stringify({
        event: "loaded",
      }),
      "*"
    );
  },
  watch: {
    selectedRating(val) {
      const rating = this.ratingSorts.find((e) => e.field === val);
      this.ratingSort = rating.sort;
      this.ratingField = rating.field;
      this.ratingFieldTitle = rating.title;
    },
  },
  computed: {
    ratingType() {
      switch (this.selectedRating) {
        case "Место по задачам":
          return 1;
        case "Место по сообществам":
          return 3;
        case "Место по внедрениям":
          return 2;
        default:
          return 0;
      }
    },
    showCenters() {
      return this.centers.filter(
        (e) => this.top10[e.elem["Субъект РФ"]] && !e.elem.hidenum
      );
    },
    maxRating() {
      return this.regions.reduce(
        (sum, curr) =>
          +curr[this.ratingField] > sum ? +curr[this.ratingField] : sum,
        0
      );
    },
    fedArr() {
      return [...new Set(this.regionDataArr.map((e) => e["Фед округ"]))];
    },
    colorArr() {
      if (this.colorGradient) {
        this.colorGradient.setGradient(
          this.ratingSort === "asc" ? "#505aea" : "#dedede",
          "#a9aee8",
          this.ratingSort === "asc" ? "#dedede" : "#505aea"
        );
        this.colorGradient.setMidpoint(this.maxRating);
        return this.colorGradient.getArray();
      }
      return [];
    },
    selectedRegion() {
      return this.regionsObj[this.overReg];
    },
    sortedRegions() {
      return [...this.regions]
        .sort((r1, r2) => {
          return r1["Субъект РФ"] - r2["Субъект РФ"];
        })
        .sort((r1, r2) => {
          const rt1 = r1[this.ratingField] || Infinity;
          const rt2 = r2[this.ratingField] || Infinity;
          return rt1 - rt2;
        });
    },
    coloredRegions() {
      return this.sortedRegions.map((r, i) => {
        let fill = undefined;
        let disable = false;
        let stroke = undefined;
        if (r[this.ratingField]) {
          fill = this.colorArr[r[this.ratingField] - 1];
        } else {
          fill = this.colorArr[this.maxRating];
        }
        if (this.selectedFed && r["Фед округ"] !== this.selectedFed) {
          fill = "#fefefe";
          stroke = "#dedede";
          disable = true;
        }
        if (this.selectedFed && r["Фед округ"] === this.selectedFed) {
        }
        if (this.top10[r["Субъект РФ"]]) {
          // fill = "#a150ea";
        }
        return {
          ...r,
          stroke,
          fill,
          disable,
        };
      });
    },
    regionDataArr() {
      return Object.keys(this.regionsObj)
        .map((key) => this.regionsObj[key])
        .sort((r1, r2) => {
          const rt1 = r1[this.ratingField]
            ? +r1[this.ratingField]
            : this.ratingSort === "asc"
            ? Infinity
            : 0;
          const rt2 = r2[this.ratingField]
            ? +r2[this.ratingField]
            : this.ratingSort === "asc"
            ? Infinity
            : 0;
          return this.ratingSort === "asc" ? rt1 - rt2 : rt2 - rt1;
        });
    },
    top10() {
      return this.top10Arr.reduce((sum, curr) => {
        sum[curr["Субъект РФ"]] = curr[this.ratingField];
        return sum;
      }, {});
    },
    top10Arr() {
      return this.regionDataArr
        .slice(0, 10)
        .filter((e) =>
          this.selectedFed ? e["Фед округ"] === this.selectedFed : true
        );
    },
  },
  methods: {
    goToRegion(item) {
      window.top.postMessage(
        JSON.stringify({
          event: "redirect",
          data: encodeURI(item["Субъект РФ"]),
        }),
        "*"
      );
    },
    listenPostMessage(callback) {
      window.addEventListener("message", callback);
      this.onDestroy = () => {
        window.removeEventListener("message", callback);
      };
    },
    async loadData(regionsData) {
      const map = axios.get("./data/map.json");
      const [mapData] = await Promise.all([map]);
      return { mapData: mapData.data, regionsData };
    },
    getCenter({ x, y, width, height }) {
      const center = {
        x: x + width / 2,
        y: y + height / 2,
      };
      return center;
    },
    registerSvgPanZoom(svgpanzoom) {
      this.svgpanzoom = svgpanzoom;
      this.svgpanzoom.zoom(0.89);
      this.svgpanzoom.pan({ x: 15, y: 130 });

      const centers = this.$refs.path;
      this.centers = centers
        .map((e, i) => {
          const box = e.getBBox();
          const center = {
            ...this.getCenter(box),
            width: box.width,
            height: box.height,
            elem: this.coloredRegions[i],
          };
          return center;
        })
        .sort((p1, p2) => {
          return p1.y - p2.y;
        });
      this.mapDiv$ = this.$refs.mapDiv;
    },
    center() {
      if (!this.svgpanzoom) return;

      this.svgpanzoom.center();
    },
    zoomIn() {
      this.svgpanzoom.zoomIn();
    },
    zoomOut() {
      this.svgpanzoom.zoomOut();
    },
    clearOver() {
      this.overReg = null;
    },
    onClick(reg) {
      if (reg) {
        if (this.overReg === reg.title) {
          this.overReg = null;
        } else {
          this.overReg = reg.title;
        }
      }
    },
    onOver(reg, i, e) {
      if (reg) {
        this.overReg = reg.title;
        this.show();
      } else {
        clearTimeout(this.tooltipTimer);
        this.showTooltip = false;
        this.overReg = null;
      }
    },
    show() {
      clearTimeout(this.tooltipTimer);
      this.tooltipTimer = setTimeout(() => {
        this.showTooltip = true;
      }, 300);
    },
    declOfNum(number, titles) {
      const cases = [2, 0, 1, 1, 1, 2];
      return titles[
        number % 100 > 4 && number % 100 < 20
          ? 2
          : cases[number % 10 < 5 ? number % 10 : 5]
      ];
    },
  },
};
</script>

<style lang="scss">
.region-tooltip {
  max-width: calc(500px);
  width: 100%;
}
</style>
<style lang="scss" scoped>
* {
  outline: none;
}
.map-info {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  padding: 15px 20px 15px 10px;
  color: #fff;
  background: rgba(#505aea, 0.9);
  max-width: 500px;
  font-size: 14px;
  font-weight: 300;
  pointer-events: none;
  line-height: 1.3;
  border-radius: 4px;
  h3 {
    margin: 0;
    padding: 0;
    margin-bottom: 7px;
  }
}
.map-controls {
  position: absolute;
  left: 10px;
  bottom: 10px;
  z-index: 2;
  .map-zoom-out,
  .map-zoom-in {
    background: #c4c7e3;
    border: none;
    width: 25px;
    height: 25px;
    margin-right: 5px;
    cursor: pointer;
    transition: 0.1s;
    color: #fefefe;
    border-radius: 50%;
    &:hover {
      background: #e0e0e0;
    }
  }
}
.top-10 {
  position: absolute;
  right: 20px;
  top: 220px;
  width: 275px;
  max-width: calc(100% - 40px);
  font-size: 12px;
  padding: 17px;
  box-sizing: border-box;
  background: rgba($color: #fff, $alpha: 0.5);
  h3 {
    color: #4d5077;
    z-index: 2;
    margin: 0;
    margin-bottom: 15px;
  }
  &-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 6px;
    font-weight: bold;
    color: #4d5077;
    cursor: pointer;
    z-index: 2;
    &.active {
      opacity: 0.8;
      .top-10-icon {
        background: #32dcac !important;
      }
    }
  }
  &-icon {
    width: 25px;
    height: 25px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    margin-right: 10px;
    flex-grow: 0;
    flex-shrink: 0;
    color: #000;
    text-align: center;
    border: 2px solid #fff;
    background: #32dcac;
    -webkit-user-select: none; /* Chrome all / Safari all */
    -moz-user-select: none; /* Firefox all */
    -ms-user-select: none; /* IE 10+ */
    user-select: none; /* Likely future */
  }
}
.point-text {
  text-anchor: middle;
  font-size: 12px;
  height: 12px;
  width: 12px;
  line-height: 12px;
  pointer-events: none;
  cursor: pointer;
  fill: #000;
  &.hover {
    fill: #fff;
  }
}
.point-circle {
  cursor: pointer;

  stroke: #fff;
  transition: 0.2s;
  fill: #32dcac;
  stroke-width: 2px;
  &.hover {
    // fill: #505aea;
  }
}
.rating-subtitle {
  font-size: 16px;
  font-weight: 200;
  color: rgba(#fff, 0.8);
  margin-right: 10px;
}
.map {
  width: 100%;
  height: 100vh;
  // width: 1060px;
  // height: 700px;
  position: relative;
  box-sizing: border-box;
  // padding-right: 100px;
}
.map-container {
  width: 100%;
  height: calc(100%);
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
}
.rating-selected-title {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 18px;
  width: 100%;
}
.tooltip-content {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  padding-left: 40px;
  white-space: nowrap;
}
.rating-number {
  font-size: 14px;
  font-weight: bold;
  margin-right: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #fff;
  border-radius: 50%;
  padding: 5px;
  background: #32dcac;
  flex-shrink: 0;
  flex-grow: 0;
  box-sizing: border-box;
  height: 30px;
  width: 30px;
  color: #000;
}
.rating-select {
  position: absolute;
  right: 20px;
  top: 120px;
  width: 275px;
  max-width: calc(100% - 40px);
}
.fed-select {
  position: absolute;
  right: 20px;
  top: 170px;
  width: 275px;
  max-width: calc(100% - 40px);
}
.map-rating {
  position: absolute;
  left: calc((100% - 220px) / 2);
  bottom: 10px;
  max-width: 450px;
  width: 100%;
  height: 10px;
  background: linear-gradient(
    90deg,
    rgba(36, 12, 0, 1) 0%,
    rgba(31, 255, 0, 1) 100%
  );
  transform: translate3d(-50%, 0, 0);
  pointer-events: none;
}
.rating-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.rating-title {
  position: absolute;
  left: 50%;
  top: -24px;
  transform: translate3d(-50%, 0, 0);
  font-size: 14px;
  width: 100%;
  text-align: center;
}
.rating-end {
  position: absolute;
  right: 0%;
  top: -24px;
  transform: translate3d(50%, 0, 0);
  font-size: 14px;
}
.rating-start {
  position: absolute;
  left: 0%;
  top: -24px;
  transform: translate3d(-50%, 0, 0);
  font-size: 14px;
}
svg {
  shape-rendering: geometricPrecision;
  max-height: 100%;
  height: 100%;
  width: 100%;
  outline: none;
}
path {
  transition: 0.2s;
  fill: #efefef;
  stroke: #fff;
  &.hover {
    fill: #32dcac !important;
    cursor: pointer;
  }
  &:focus {
    outline: none;
    box-shadow: none;
  }
  &.disable {
    pointer-events: none;
  }
}
.tooltip {
  &-items {
    margin-bottom: 15px;
    width: 100%;
    flex-wrap: wrap;
    display: flex;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      left: -26px;
      top: 1px;
      height: 100%;
      width: 2px;
      background: #32dcac;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &-item {
    font-size: 14px;
    display: inline-flex;
    align-items: center;
