<script>
import axios from "axios";
const fields = {
  "Задачи, прошедшие во 2 этап": "selected",
  "Поставленные задачи": "assigned",
  "Решённые задачи": "solved",
  "Команды-победители": "teams",
  "Внедрённые решения": "injected",
  "Ссылка на Data-DebatЫ": "debates",
  "Субъект РФ": "name",
  "Фед округ": "district",
  "Цифровой наставник": "mentor",
  "Решения в СМАРТЕКЕ": "smarteka",
};
export default {
  name: "App",
  props: ["regionName"],
  data() {
    return {
      loading: true,
      regionData: null,
      activity: null,
      tabPosition: "projectFeedback",
      onDestroy: () => {},
    };
  },
  async created() {
    this.listenPostMessage(async (e) => {
      try {
        const ev = JSON.parse(e.data);
        if (ev.event === "loadData") {
          console.log("loadData", ev.data);
          await this.loadData(ev.data);
        }
      } catch (error) {}
    });
    window.top.postMessage(
      JSON.stringify({
        event: "loaded",
      }),
      "*"
    );
    if (
      location.hostname === "localhost" ||
      location.hostname === "127.0.0.1" ||
      location.hostname === ""
    ) {
      this.loadData(await this.loadActivity());
    }
  },
  beforeDestroy() {
    this.onDestroy();
  },
  computed: {
    regionActivity() {
      const region = {};
      const years = new Set();
      const r = Array.isArray(this.activity)
        ? this.activity.find((e) => e["Субъект РФ"] === this.regionName)
        : null;
      for (const key in r) {
        if (Object.hasOwnProperty.call(r, key)) {
          const element = r[key];
          const yearData = /([0-9]{4})\: (.+)/g.exec(key);
          if (yearData) {
            if (!region[fields[yearData[2]]]) {
              region[fields[yearData[2]]] = [];
            }
            region[fields[yearData[2]]].push(
              ...(element || []).map((e) => ({
                rank: e["Номер"] ? +e["Номер"] : null,
                name: e["Заголовок"] || null,
                link: e["Ссылка"] || null,
                linkId: e["Ссылка-идентификатор"] || null,
                smartekaLink: null,
                year: +yearData[1] || null,
              }))
            );
            years.add(+yearData[1] || null);
          } else {
            region[fields[key]] = Array.isArray(element)
              ? element.map((e) => ({
                  rank: e["Номер"] ? +e["Номер"] : null,
                  name: e["Заголовок"] || null,
                  link: e["Ссылка"] || null,
                  linkId: e["Ссылка-идентификатор"] || null,
                }))
              : element;
          }
        }
      }
      console.log(region, r);
      const solutions = [];
      const solutionsObj = {};
      let mentorObj = {};
      let smartekaLinksObj = {};
      if (Array.isArray(region.smarteka)) {
        smartekaLinksObj = region.smarteka.reduce((sum, curr) => {
          if (curr.linkId) {
            sum[curr.linkId] = curr;
          } else {
            console.log("Нет ссылки идентификатора", curr);
          }
          return sum;
        }, {});
        console.log(smartekaLinksObj);
      }
      if (Array.isArray(region.mentor)) {
        mentorObj = region.mentor.reduce((sum, curr) => {
          if (curr.link) {
            sum[curr.link] = curr;
          } else {
            console.log("Нет ссылки идентификатора", curr);
          }
          return sum;
        }, {});
        console.log(mentorObj);
      }

      if (Array.isArray(region.injected)) {
        region.injected.forEach((e) => {
          if (e.link) {
            solutionsObj[e.link] = e;
            if (smartekaLinksObj[e.link]) {
              e.smartekaLink = smartekaLinksObj[e.link].link;
            }
            if (mentorObj[e.link]) {
              e.isMentor = true;
            }
            solutions.push(e);
          } else {
            console.error("Отсутствует ссылка:", e);
          }
        });
      }
      // if (Array.isArray(region.mentor)) {
      //   region.mentor.forEach((e) => {
      //     if (e.link && solutionsObj[e.link]) {
      //       solutionsObj[e.link].mentor = true;
      //     } else {
      //       console.error("Отсутствует ссылка:", e);
      //     }
      //   });
      // }
      const community = [...years].map((year) => {
        return {
          year: year,
          dataDebates: (region.debates || []).filter((d) => d.year === year),
          dataHack: (region.hack || []).filter((d) => d.year === year),
          teams: region.teams.filter((e) => e.year === year),
        };
      });
      const tasks = [...years].map((year) => {
        return {
          year: year,
          assigned: (region.assigned || []).filter((e) => e.year === year)
            .length,
          selected: region.selected.filter((e) => e.year === year),
          solved: region.solved.filter((e) => e.year === year),
        };
      });
      return {
        tasks: tasks.sort((e1, e2) => e2.year - e1.year),
        implementation: solutions,
        community: community.sort((e1, e2) => e2.year - e1.year),
      };
    },
  },
  methods: {
    listenPostMessage(callback) {
      window.addEventListener("message", callback);
      this.onDestroy = () => {
        window.removeEventListener("message", callback);
      };
    },
    async loadActivity() {
      const res = await axios.get("./data/activity.json");
      return res.data;
    },
    async loadRegions() {
      const res = await axios.get("./data/regions.json");
      return res.data;
    },
    async loadData(data) {
      this.activity = data;
      this.loading = false;
    },
  },
};
</script>

